* {
  box-sizing: border-box;
}

.lecture {
  background: #ccc;
}

.last-row {
  border-bottom: 3px solid black;
}

td {
  font-size: 2em;
}

.empty-day {
  border-bottom: 3px solid black;
  background: gray;
}

.empty-day td:nth-child(1) {
  border-bottom: 3px solid black;
  background: white;
}

.cell {
  border: 1px solid black;
}

.place-head {
  border: 1px solid black;
}

td span {
  display: block;
}

td {
  border: 1px solid black;
}

td {
  flex-grow: 0;
}

.important {
  padding: 2px;
  display: inline;
  background-color: black;
  color: white;
}

.quiz {
  background: #002984;
}

.hands-on {
  background: darkblue;
}

.task-delivery {
  background: slateblue
}

.cancelled {
  background: green;
}